<template>
    <div>
        <mu-button class="demo-button" color="primary" @click="prompt()">Alert</mu-button>
    </div>
</template>

<script>
export default {
  methods: {
    prompt() {
      this.$prompt("输入账号密码", {
        okLabel: "提交"
      }).then(() => {
        this.$toast.message("提示信息");
      });
    }
  }
};
</script>

<style scoped>
</style>



